.page-heading,
.page-contents {
  display: grid; }

.page-heading {
  grid-template-columns: 1fr;
  grid-template-areas: "heading-caption";
  grid-column-gap: 1rem; }

.heading-caption {
  grid-area: heading-caption; }

.page-contents {
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas: "content-1 . . ." "content-2 content-2 content-2 content-2";
  grid-column-gap: 1rem;
  grid-row-gap: 2rem; }

.content-1 {
  grid-area: content-1;
  min-width: 160px;
  display: flex;
  flex-direction: row; }

.content-2 {
  grid-area: content-2; }

.q-card__section {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
          hyphens: auto; }

.comment-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.filter-search-box {
  min-width: 237px;
  width: 90%; }

@media (max-width: 360px) {
  .page-heading {
    grid-template-columns: 1fr;
    grid-template-areas: "heading-caption" "heading-stat-1"; } }

@media (min-width: 1820px) {
  .page-contents {
    grid-template-columns: repeat(4, 494px);
    grid-template-areas: "content-1 . . . " "content-2 content-2 content-2 .";
    grid-column-gap: 1rem;
    grid-row-gap: 2rem; } }
